import api from '../baseUrl';

export default (guid, key, items, value) => {

  return api.post('/commands', {
    "id": guid,
    "method": "set",
    "uri":  encodeURI("/resources/"+ items.trim()),
    "type": "application/json",
    "resource": value

  },{
      headers: {
        'Content-Type': 'application/json',
        'Authorization': key
  }});
}
