import React, { useState, useEffect } from 'react';

import guid from '../../utils/guid';

import { NotificationContainer } from 'react-notifications';
import { showToast } from '../../api/commomService'

import Select from '../Select/index'
import getMessageTemplate from '../../services/getMessageTemplate';
import resourceServices from '../../services/handleGetResourceItems';
import resourceSetServices from '../../services/handleMergeResource';
import { set_builder } from '../../constants/application-actions'
import createUser from '../../services/handleSetAuth';
import notification from '../../services/sendMessageNotification';


function PluginDesk({ info, tenantId, accessKey, shortName, nameSpace, builder }) {



  const [nameSpaceState, setNameSpaceState] = useState(nameSpace);

  const [templates, setTemplates] = useState([]);
  const [dataSubBot, setDataSubBot] = useState([]);
  const [loading, setLoading] = useState(false);
  const [subBots, setSubBots] = useState(JSON.parse(localStorage.getItem('details')));
  const [templateSubBot, setTemplateSubBot] = useState('Selecione um bot');
  const [builderKey, setBuilderKey] = useState("");
  const [templateSubBotFlowId, setTemplateSubBotFlowId] = useState('Selecione um bloco');
  const [message, setMessage] = useState([])
  const [extension, setExtension] = useState(false)
  const [botList, setBotList] = useState([])
  const [tenant] = useState(localStorage.getItem('plugin-transfer-tenantId'))

  useEffect(async() => {

    fnAuxSubBots()
    fnAuxNameSpace()
    fnAuxTemplates()

    const resultBuilder = atob(set_builder).split(':')[1];

    console.log(resultBuilder)
    setBuilderKey(resultBuilder);


  

  }, []);



  const fnAuxTemplates = async () => {

    const arrayTemplates = [];

    const response = await getMessageTemplate(guid(), localStorage.getItem('info'));


    const templates = response.data.resource.data.filter((t) => t.name && t.name.split('_')[0] !== 'sample' && t.status === 'APPROVED');

    templates.map((template) => {
      arrayTemplates.push({
        id: template.name,
        value: template.name,
        label: template.name,
        selected: false
      })
    });

    setMessage(arrayTemplates);

  }

  const handleRecordResource = async () => {

    const config = {
      "system": [{ "url": "/ticket", "index": "tab3", "label": "Transferncia", "icon": "transfer" }],
      "attendants": [],
      "app": "Transferencia de tickets",
      "listBots": botList,
      "headers": {
        "identifier": shortName,
        "accessKey": accessKey
      }
    }

    const resourceKey = tenant;

     createUser(resourceKey);

    const resultResource = await resourceServices(guid(), builderKey, resourceKey);
    notification(resourceKey, 'Transferncia de tickets')

    const resultFilter = !!resultResource.data.resource.system.filter((x) => x.label === 'Transferncia').length

    if (resultFilter) {
      showMessage("Extenso j esta cadastrada!", "success")
        setExtension(true)
        return
    }



    resultResource.data.resource["listBots"] = botList;



    if (resultResource.data.status === "failure") {

      resourceSetServices(guid(), builderKey, resourceKey, config)

    } else {

      const system = [...resultResource.data.resource.system, { "url": "/ticket", "index": "tab3", "label": "Transferncia", "icon": "transfer" }]
      resultResource.data.resource.system = system;

      resourceSetServices(guid(), builderKey, resourceKey, resultResource.data.resource);

    }

    showMessage("Extenso cadastrada com sucesso", "success")
    setTimeout(() => {
      window.location.reload();
    }, 3000);

  }

  const fnAuxSubBots = () => {

    const arraySubBots = [];
    subBots.filter((bots) => { if (bots.flowId !== '') arraySubBots.push({ label: bots.longName, value: bots.longName }); })


    const template_list = arraySubBots.map((template) => ({
      id: template.label,
      value: template.value,
      label: template.label,
      selected: false
    }));
    setTemplates(template_list);
    setDataSubBot(arraySubBots);
  }
  const fnAuxNameSpace = () => {

    setTimeout(() => {

      console.log(nameSpace)
      setNameSpaceState(nameSpace)

    }, 7000)
  }


  const handleSetEventSubBot1 = async (event) => {


    const nameSubBot = event

    let arraySubBots = [];

    for (const iterator of nameSubBot) {

      const { identifier, accessKey } = subBots.filter((e) => e.longName === iterator)[0];
      const KEY = 'Key ' + btoa(`${identifier}:` + atob(accessKey));

      arraySubBots.push({
        key: KEY,
        id: identifier,
        displayName: iterator
      })

    }

    setBotList(arraySubBots)
    console.log(arraySubBots)

  }


  function showMessage(msg, type) {

    setTimeout(() => {
      showToast({
        type: type,
        message: msg
      })
      setLoading(false);
    }, 900);
  }


  return (
    <div className="container w-100">

      <h1>Configurao</h1>

      <div className="mt-2 mb-2">
        <div className="row">

          <div className="col-8">
            <Select
              icon="filter"
              name="template"
              label="Selecione os chatbots de Atendimento"
              placeholder="Selecione"
              multiple={true}
              searchable={true}
              options={templates}
              selected={templates}
              onChange={($event) => handleSetEventSubBot1($event)}
            />

          </div>


          <hr />
        </div><br /><br />



        <br />
        <span><strong>OBS:</strong> Aps seguir os passos acima, clique no link abaixo para instalar o plugin na loja do Google Chrome</span><br /><br />
        <span ng-if="tab.tabHref">
          <a href="https://chrome.google.com/webstore/detail/parsehub/khhjfhfaebfhlfpfkmgkjfiejhmnipfg" class="tab-href" target="_blank" style={{ color: 'black' }}>Clique aqui! <i class="icon-external-link icon icon-xss mb2"></i></a>
        </span>
        <br />
        <br />
        <hr />
      </div>


      <div className="mt-5 col-12">

        {!extension && <bds-button icon="builder-publish-bot" style={{ display: 'flex', justifyContent: 'start' }} size="short" onClick={handleRecordResource} >Ativar Extenso</bds-button>}
        {extension && <bds-button icon="builder-publish-bot" style={{ display: 'flex', justifyContent: 'start' }} size="short" >Ativado</bds-button>}

      </div>
      <NotificationContainer />
    </div>
  );

}


export default PluginDesk;